import React, {Component} from 'react';
import './style.css'
import Loading from './containers/loading/Loading'
import ImageSlider from './containers/ImageSlider'
import styled from 'styled-components';

const Container = styled.h1`
  width: 100%;
  height: 800px;
  display: flex;
  justify-content: center;
  align-items: center;
`;

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      isImgSlider: false
    };
    this._getImgSlider = this._getImgSlider.bind(this);
  }

  _getImgSlider(visible) {
    this.setState({
      isImgSlider: visible,
    },() => {
      console.log('parent',visible);
    });
  }

  render() {
    console.log('render')
    return (
      <Container>
        <Loading getImgSlider={this._getImgSlider} />
        <div style={{position:'absolute', display: this.state.isImgSlider ? 'block':'none'}}>
          <ImageSlider isImgSlider={this.state.isImgSlider} />
        </div>
      </Container>
    );
  }
}


export default App;



